import React, { Component } from 'react';
import { Footer, PatternBg } from './Utils.js';
import Web3 from 'web3';
import ReactTooltip from 'react-tooltip';
import axios from 'axios';
import NavBar from './NavBar.js';
import { Modal, ModalBody, ModalFooter } from 'reactstrap';
import * as handlers from './web3handlers.js';

import logo from './img/SVG/logo.svg';
import './App.css';

import TXQueueContainer from './TXQueueContainer.js';
import SchedulerContainer from './SchedulerContainer.js';
import TXCContainer from './TXCContainer.js';
import NoticesContainer from './NoticesContainer.js';
import DepositContainer from './DepositContainer.js';
import BalanceContainer from './BalanceContainer.js';
import SendRawContainer from './SendRawContainer.js';


class App extends Component {
  constructor(){
    super();

    this.state = {
      addressList : [],
      queue : {},
      blockNumber : 0,
      balance: 0,
      netID: 1,
      hasTXC: false,
      blocksFormat: true,
      server: 'devrem',
      modal: false,
    }

    this.web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

    this.mew = "https://www.myetherwallet.com/signmsg.html";

    this.addresses = {
      [42]: {
        eths: "0xa0475d202be30019e56306cc08f8998887135258",
        eths_old: "0xe49bb13f1e57de8364586bbb82bf79d0c8aac074",
        txcsale: "0x2ffbee1b7eda9d6187f4f875899c9f8f28d927ed",
        txc: "0x12C942fDbE9981E68DC153CC92dA2e2c301F5a9A",
      },
      [1]: {
        eths: "0xf99E87098f4F1C8d8bd6a3df7181B3acdc21848A",
        txcsale: "0xa4b01a89162141c58505ea43db2d3bda71a325c2",
        txc: "0x67e35c41060a988f59e2bcb2e0f09b6978fb6614",
      }
    };

    this.urls = {
      'dev': "http://localhost:5000",
      'devrem': "http://txcast.io:5000",
      'prod': "https://api.txcast.io",
    }

    this.abiTypes = {
      eths: {
        withoutSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
        ],
        withSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
          'bytes',   // signature 
        ],
      }
    }

    this.modalData = {
      signData: {
        body: ({params}) => {
          const total = this.web3.utils.toBN(params.dataArray.reward).add(this.web3.utils.toBN(params.dataArray.amount));
          const hasBalance = total.lte(this.web3.utils.toBN(params.balance));

          return (
            <ModalBody>
              <h2 className="text-black">TX Request</h2>
              <p>You are about to sign a TX request. This can still be cancelled before someone broadcasts it.</p>
              <p><h5 className="text-black">Your input data is the following:</h5>
                Receiver:<small className="text-monospace float-right"> {params.dataArray.address}</small>
                <br/>Schedule: <span className="float-right">Block {params.dataArray.schedule}</span>
                <br/>TX hash: <span className="text-monospace float-right">{this.web3.utils.hexToAscii(params.hash)}</span></p>
              <hr/>
              <p>Amount: <span className="float-right">{this.web3.utils.fromWei(params.dataArray.amount,'ether')} ETH</span>
                <br/>Reward: <span className="float-right">{this.web3.utils.fromWei(params.dataArray.reward,'ether')} ETH</span></p>
              <hr/>
              <p>Total TX request: <span className="float-right">{this.web3.utils.fromWei(total.toString(),'ether')} ETH</span>
                <br/>Current balance: <span className={"float-right text-" + (hasBalance ? "success" : "danger")}>{this.web3.utils.fromWei(params.balance.toString(),'ether')} ETH</span>
                <br/>{!hasBalance && <small className="text-danger float-right"><i className="fas fa-exclamation-triangle"></i> The total amount you're requesting is higher than your balance. Make sure to deposit enough ETH to cover the TX amount and reward.</small>}</p>
            </ModalBody>
          )},
        footer: ({toggle, action, params}) => (
          <ModalFooter>
            <button className="btn btn-info" onClick={toggle}>Back</button>
            <button className="btn btn-success" onClick={() => action(params.hash, params.signer, params.dataArray)}>Sign TX</button>{' '}
          </ModalFooter>
        ),
      },

      cancelData: {
        body: () => (
          <ModalBody>
            <h2 className="text-black">TX Cancellation</h2>
            <p>Cancelling a TX has two steps: Cancelling the request on the blockchain and removing the request from the TXCast server.</p>
            <p>You will be prompted for two signatures:
              <br/>one with a 0.001 ETH cost to cancel on the blockchain,
              <br/>and one with no cost to remove the request.</p>
            <p>It is <b>absolutely necessary</b> to send the 0.001 ETH transaction,
              since that is the 100% guarantee that your request is fully invalidated. The zero cost transaction will remove your request from the servers, but if someone has a copy of your signature, it can still be used.</p>
          </ModalBody>
        ),
        footer: ({toggle, action, params}) => (
          <ModalFooter>
            <button className="btn btn-info" onClick={toggle}>Back</button>
            <button className="btn btn-danger" onClick={() => action(params.signature, params.nonce)}>Cancel TX</button>{' '}
          </ModalFooter>
        ),
      }
    }

    this.toggle = this.toggle.bind(this);
    this.updateBlock = this.updateBlock.bind(this);

    this.cancelTX = handlers.cancelTX.bind(this);
    this.signTX = handlers.signTX.bind(this);
    this.sendTX = handlers.sendTX.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  } 

  async componentDidMount() {
    try {
      await window.ethereum.enable();

      const a = await this.web3.eth.getAccounts();
      const id = await this.web3.eth.net.getId();

      await this.setState({addressList: a, netID: id});

      const address = a[0];
      const callData = {
        to: this.addresses[this.state.netID].txc,
        data:'0x70a08231' + this.web3.eth.abi.encodeParameters(
          [
            'address',
          ],
          [address]
        ).substr(2)
      }

      this.web3.eth.call(callData).then((b,s) => {
        this.setState({hasTXC: parseInt(b) > 0});
      });

      this.web3.currentProvider.publicConfigStore.on('update', (update) => {
        this.setState({addressList:[this.web3.utils.toChecksumAddress(update.selectedAddress)], netID:update.networkVersion});
      });

      setInterval(this.updateBlock, 5000);
    } catch (e) {
      console.error(e,"no web3");
    }
  }

  fetchData() {
    console.log(this.state)
    const req = {
      url: this.urls[this.state.server] + '/signature',
      method: 'GET',
    };

    axios(req).then(ans => {
      const queue = ans.data.reduce((acc, v) => { acc[v[0]] = v[1]; return acc; }, {});
      this.setState({queue: queue});
    });

  }

  async updateBlock() {
    const latestBlock = await this.web3.eth.getBlock('latest');
    if(latestBlock !== null && /[0-9]*/.test(latestBlock)) this.setState({blockNumber: latestBlock.number});
  }

  render() {
    const netid = this.state.netID;
    const ethsAddress = this.addresses[netid].eths;
    const ethsUrl = "https://" + (netid === "1" ? "www" : "kovan") + ".etherscan.io/address/" + ethsAddress;
    const coinbaseUrl = "https://" + (netid === "1" ? "www" : "kovan") + ".etherscan.io/address/" + this.state.addressList[0];

    const data = {netid: netid, ethsAddress: ethsAddress, ethsUrl: ethsUrl, coinbaseUrl: coinbaseUrl}

    const modal = this.state.modal ? (
      <ActionModal
        innerHTML={this.state.innerHTML}
        params={this.state.params}
        action={this.state.action}
        toggle={this.toggle}
        modal={this.state.modal}
      />) : "";

    return (
      <div>
        <PatternBg />
        <ReactTooltip />
        <NavBar logo={logo} {...this.state} {...data}/>
        {modal} 
        <div className="container-fluid px-4 mt-4">
          <div className="row">
            <div
              className="col-lg-5"
            >
              <TXQueueContainer
                account={this.state.addressList[0]}
                queue={this.state.queue}
                blockNumber={this.state.blockNumber}
                onFulfill={handlers.onFulfill.bind(this)}
                onCancel={handlers.onCancel.bind(this)}
                fetchData={this.fetchData.bind(this)}
                fromWei={this.web3.utils.fromWei}
              />
              <SendRawContainer 
                onSendRaw={handlers.onSendRaw.bind(this)}
              />
            </div>
            <div className="col-lg-7">
              <div className="row">
                <div
                  className="col-lg-7"
                >
                  <SchedulerContainer
                    onSign={handlers.onSign.bind(this)}
                  />
                  <TXCContainer
                    onBuy={handlers.onBuy.bind(this)}
                  />
                </div>
                <div
                  className="col-lg-5"
                >
                  <DepositContainer
                    address={this.state.addressList[0]}
                    onDeposit={handlers.onDeposit.bind(this)}
                  />
                  <BalanceContainer
                    address={this.state.addressList[0]}
                    balance={this.state.balance}
                    onBalance={handlers.onBalance.bind(this)}
                  />
                  <NoticesContainer/>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const ActionModal = (props) => (
  <Modal isOpen={props.modal} toggle={props.toggle}>
    {props.innerHTML.body({params:props.params})}
    {props.innerHTML.footer({toggle:props.toggle, action:props.action, params:props.params})}
  </Modal>    
);


export default App;
